* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  line-height: 1.7em;
  font-family: "Roboto", sans-serif;
}

.nav-mobile-container {
  display: none;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #184357;
  position: relative;
  min-height: 100px;
  z-index: 1;
}

.logo {
  font-size: 2rem;
  color: #fff;
  padding: 2rem;
  text-decoration: none;
}

header nav ul {
  list-style: none;
  display: flex;
}
header nav li {
  padding: 1rem;
}
header nav a {
  font-size: 1.4rem;
  color: #fff;
  text-decoration: none;
}
header nav a:hover {
  color: #af9f9f;
}
header nav a:active {
  color: #fff;
}

#hamburger {
  display: none;
}

#hamburger:hover {
  cursor: pointer;
}

@media all and (max-width: 800px) {
  .nav-desktop-container {
    display: none;
  }
  #hamburger {
    display: flex;
    font-size: 1.4rem;
    padding: 1rem;
    color: #fff;
  }
  .nav-mobile-container {
    width: 40%;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    display: none;
    background-color: #686161;
  }
  .nav-mobile {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    text-align: center;
    padding: 0.5rem;
  }
  #close {
    float: right;
    color: #fff;
    font-size: 1.4em;
  }
  #close:hover {
    cursor: pointer;
  }
}
@media all and (max-width: 600px) {
  .logo {
    font-size: 1.7rem;
    padding: 1rem;
  }
  .nav-mobile-container {
    width: 70%;
  }
}
@media all and (max-width: 400px) {
  .nav-mobile-container {
    width: 100%;
  }
  .logo {
    font-size: 1.3rem;
  }
}
footer {
  background-color: #23627f;
  min-height: 80px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.dark-mode header {
  background-color: #181818;
}
.dark-mode body {
  background-color: #184357;
}
.dark-mode .sec h1,
.dark-mode p {
  color: #fff;
}
.dark-mode footer {
  background-color: #686161;
}

@media (prefers-color-scheme: dark) {
  header {
    background-color: #181818;
  }
  body {
    background-color: #184357;
  }
  .sec h1 {
    color: #fff;
  }
  .sec p {
    color: #fff;
  }
  footer {
    background-color: #686161;
  }
  .sass-section,
  .sass-section-two {
    background-color: #184357;
  }
}
.light-mode header {
  background-color: #184357;
}
.light-mode body {
  background-color: #e6e6e6;
}
.light-mode .sec h1,
.light-mode p {
  color: #000;
}
.light-mode footer {
  background-color: #23627f;
}

p {
  font-size: 1rem;
}

h1 {
  font-size: 2rem;
}

@keyframes colorTransition {
  0% {
    background-color: blue;
    left: 0;
  }
  25% {
    left: 10px;
  }
  50% {
    background-color: yellow;
    left: 20px;
  }
  75% {
    left: 10px;
  }
  100% {
    background-color: blue;
    left: 0;
  }
}
@keyframes changeBackgroundColor {
  0% {
    background-color: yellow;
  }
  100% {
    background-color: blue;
  }
}
@keyframes linearGradient {
  25% {
    background: linear-gradient(30deg, var(--color1), var(color2));
  }
  50% {
    --color1: blue;
    --color2: yellow;
  }
  75% {
    background: linear-gradient(40deg, var(--color1), var(--color2));
  }
  100% {
    background: linear-gradient(50deg, var(--color1), var(--color2));
  }
}
@keyframes rotateEl {
  from {
    transform: rotate(4deg);
  }
  to {
    transform: rotate(-4deg);
  }
}
@keyframes scaleEl {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(1.2, 1.2);
  }
}
@keyframes glitterText {
  0% {
    color: red;
  }
  25% {
    color: blue;
  }
  50% {
    color: green;
  }
  75% {
    color: yellow;
  }
  100% {
    color: purple;
  }
}
.flex-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sec {
  display: flex;
  flex-direction: column;
  width: 50%;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0.5rem;
  margin-bottom: 40px;
}

.sec h1 {
  margin-top: 60px;
  margin-bottom: 30px;
}

.hiddenOne,
.hiddenTwo {
  overflow: hidden;
}

@media all and (max-width: 1000px) {
  .sec {
    width: 80%;
  }
}
@media all and (max-width: 500px) {
  .sec {
    width: 90%;
  }
  .flex-container {
    width: 95%;
    margin: 0 auto;
  }
}
.sass-section, .sass-section-two {
  padding: 0.5rem 0.5rem;
  margin: 1rem;
  background-color: #fff;
  width: 40%;
  border-radius: 5px;
}

.sass-section h1, .sass-section-two h1,
.sass-section-two h2 {
  text-align: center;
  margin-bottom: 2rem;
}

.sass-section-two {
  background-color: #e6e6e6;
  margin-bottom: 120px;
}

.list {
  width: 90%;
  margin: 0 auto;
}

.non-dot {
  list-style: none;
  font-weight: bold;
}

.sass-container {
  margin-top: 50px;
}

@media all and (max-width: 1100px) {
  .sass-section,
  .sass-section-two {
    width: 60%;
  }
}
@media all and (max-width: 800px) {
  .sass-section,
  .sass-section-two {
    width: 70%;
  }
}
@media all and (max-width: 500px) {
  .sass-section,
  .sass-section-two {
    width: 90%;
  }
}
@property --color1 {
  syntax: "<color>";
  inherits: true;
  initial-value: yellow;
}
@property --color2 {
  syntax: "<color>";
  inherits: true;
  initial-value: blue;
}
.animation-box {
  max-width: 500px;
  margin: 0 auto;
  border: 1px solid #000;
  background-color: yellow;
  animation-name: changeBackgroundColor;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  margin-top: 50px;
  padding: 1rem;
  margin-bottom: 50px;
}

.animation-flex {
  display: flex;
}

.ani-width {
  width: 50%;
  margin: 0 auto;
}

.animation1 {
  position: relative;
  background-color: #181818;
  margin: 0 auto;
  margin-top: 50px;
  animation-name: colorTransition;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
  width: 30px;
  height: 30px;
}

.animation2, .animation6 {
  position: relative;
  background-color: #181818;
  margin: 0 auto;
  margin-top: 50px;
  animation-name: colorTransition;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
  width: 15px;
  height: 15px;
}

.animation3, .animation5 {
  position: relative;
  background-color: #181818;
  margin: 0 auto;
  margin-top: 50px;
  animation-name: colorTransition;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
  width: 40px;
  height: 10px;
}

.animation4 {
  position: relative;
  background-color: #181818;
  margin: 0 auto;
  margin-top: 50px;
  animation-name: colorTransition;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
  width: 70px;
  height: 100px;
}

.animation7, .animation8 {
  position: relative;
  background-color: #181818;
  margin: 0 auto;
  margin-top: 50px;
  animation-name: colorTransition;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
  width: 10px;
  height: 50px;
}

.animation9, .animation10 {
  position: relative;
  background-color: #181818;
  margin: 0 auto;
  margin-top: 50px;
  animation-name: colorTransition;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
  width: 40px;
  height: 10px;
}

.ani-text {
  width: 50%;
  margin: 5% auto;
  padding: 0.6rem 0.6rem;
}
.ani-text h1 {
  text-align: center;
  margin-bottom: 1rem;
}

.btn {
  margin: 1em auto 0;
  padding: 0.6em 1.2em;
  display: block;
  border: 1px solid #000;
  border-radius: 5px;
  font-size: 1.5rem;
  background: linear-gradient(50deg, var(--color1), var(--color2));
  color: #000;
  animation: linearGradient 8s infinite alternate-reverse;
}

.btn:hover {
  cursor: pointer;
  animation: rotateEl 0.3s linear infinite alternate, scaleEl 0.4s linear;
}

.btn:active {
  animation: rotateEl 2s;
}

#hidden {
  margin-top: 2rem;
  border: 5px solid #000;
  padding: 1rem;
  background-color: beige;
  min-height: 100px;
}
#hidden h2 {
  text-align: center;
}

#hidden:hover {
  animation: scaleEl 2s forwards;
}

.text-color {
  transition: color 0.3s;
}

.text-color:hover {
  animation: glitterText 4s infinite;
  cursor: pointer;
}

.hover-color {
  color: red;
}

@media all and (max-width: 800px) {
  .ani-text {
    width: 70%;
  }
}
@media all and (max-width: 600px) {
  .animation-box {
    width: 90%;
  }
  .ani-text {
    width: 90%;
  }
}
button {
  width: 100px;
  height: 40px;
  background-color: #798b97;
  font-size: 0.9rem;
  border-radius: 5px;
  margin-top: 20px;
  margin-bottom: 20px;
}

button:hover {
  cursor: pointer;
  background-color: #fff;
}

button:active {
  background-color: #798b97;
}

.image {
  width: 700px;
  height: auto;
  border: 5px solid #000;
  border-radius: 10px;
  max-width: 100%;
  display: block;
  position: relative;
}